/**
 * urljs
 * @param key
 * @returns {*}
 */
export function currentUrlToParams(key = null) {
    let paramsUrl = (window.location.href).split('?');
    if(paramsUrl.length<2)return key ? null : {};
    let paramsArr = paramsUrl[1].split('&');
    let paramsData = {}
    paramsArr.forEach(r=>{
        let data = r.split('=')
        paramsData[data[0]] = data[1]
    })
    if(key)return paramsData.hasOwnProperty(key) ? paramsData[key] : null;
    return paramsData;
}

/**
 * url
 */
export function initUrlParams() {
    window.URlPARAMS = currentUrlToParams();
}

/**
 * 
 * @param obj
 * @returns {any}
 */
export function clone(obj) {
    let strData = JSON.stringify(obj)
    return JSON.parse(strData);
}

/**
 * key
 * @param key
 * @param desc   true 
 * @returns {function(*, *): number}
 */
export function objSort(key,desc) {
    return function(a,b){
        //return desc ? ~~(parseInt(a[key]) < parseInt(b[key])) : ~~(parseInt(a[key]) > parseInt(b[key]));json
        return desc ? ((parseInt(a[key]) < parseInt(b[key]))?1:((parseInt(a[key]) > parseInt(b[key]))?-1:0)):((parseInt(a[key]) < parseInt(b[key]))?-1:((parseInt(a[key]) > parseInt(b[key]))?1:0))  //

    }
}

